import "./styles/index.scss";

const form = document.querySelector("#contacts-form");

form.querySelectorAll(".modal-selection").forEach((input) => {
  if (input.querySelector(".error")) {
    input.querySelector("input").classList.add("error");
  } else {
    input.querySelector("input").classList.remove("error");
  }
});

$(document).ready(function () {
  //FORM VALIDATE
  $("#contacts-form").validate({
    rules: {
      mail: {
        required: true,
        email: true,
      },
      name: {
        required: true,
      },
      phone: {
        required: true,
      },
    },
    messages: {
      mail: {
        required: "Введите email",
        email: "Отсутсвует символ @",
      },
      name: {
        required: "Введите имя",
      },
      phone: {
        required: "Введите телефон",
      },
    },
    submitHandler: function (form) {
      ajaxFormSubmit();
    },
  });

  // Функция AJAX запрса на сервер

  function ajaxFormSubmit() {
    let string = $("#contacts-form").serialize(); // Соханяем данные введенные в форму в строку.

    //Формируем ajax запрос
    $.ajax({
      type: "POST", // Тип запроса - POST
      url: "php/mail.php", // Куда отправляем запрос
      data: string, // Какие даные отправляем, в данном случае отправляем переменную string

      //   // Функция если все прошло успешно
      //   success: function (html) {
      //     $("#contacts-form").slideUp(800);
      //     $("#answer").html(html);
      //   },
    });
    // Чтобы по Submit больше ничего не выполнялось - делаем возврат false чтобы прервать цепчку срабатывания остальных функций
    return false;
  }
});
